import React from 'react'

const Summary = props => {
    const { data } = props;

    return (
        <div >
            <h1>Summary:</h1>
            <div>{`Is Equal sentence: ${data.isEqual}`}</div>
            <div>{`Time took: ${data.time} seconds`}</div>
            <div>{`Backspace Pressed ${data.backspaceCounter}`}</div>

        </div>
    )
}

export default Summary