import React from 'react';
import Languages from '../config/languages'


class SentenceWriter extends React.Component {

    state = {
        sentence: Languages[this.props.language][Math.floor(Math.random() * Languages[this.props.language].length)],
        value: "",
        enterCounter: 0,
        backspaceCounter: 0,
        time: 0,
        interval: null,
        isEqual: false
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.language !== prevProps.language) {
            let sentence = Languages[this.props.language][Math.floor(Math.random() * Languages[this.props.language].length)]
            this.setState({
                sentence: sentence,
                value: "",
                enterCounter: 0,
                backspaceCounter: 0,
                time: 0,
                interval: null,
                isEqual: false
            })

        }
    }

    componentDidMount() {
        this.startTimer();
        const self = this;
        document.addEventListener('keypress', (e) => {
            if (e.key === 'Enter') {
                clearInterval(this.state.interval);
                self.setState({ enterCounter: 0 });
                if (self.state.sentence === self.state.value) {
                    self.setState({ isEqual: true })
                }

                self.setState({ ...this.state }, () => {
                    self.props.showSummaryCallback(this.state)
                })
            }

        });


    }


    handleChange(event) {
        this.setState({ value: event.target.value })

        if (this.state.sentence === event.target.value) {
            this.setState({isEqual: true }, () => {
                this.props.showSummaryCallback(this.state)
            })
        }
    }

    startTimer() {
        let interval = setInterval(this.timer, 1000);
        this.setState({ interval: interval });
    }

    timer = () => {
        this.setState({ time: this.state.time + 1 });
    };

    handleKeyDown = (event) => {
        if (event.key === 'Backspace') {
            this.setState({ backspaceCounter: ++this.state.backspaceCounter })
        }

    }


    render() {
        return (
            <>
            <h2>Text to type:</h2>
                <div>{`"${this.state.sentence}"`}</div>
                <textarea ref={(ref)=>this.textarea = ref} rows={5} style={{fontSize:"20px", margin:"20px"}}
                    value={this.state.value}
                    onChange={(event) => this.handleChange(event)}
                    onKeyDown={(event) => this.handleKeyDown(event)}
                />

                <div>{`Time: ${this.state.time}`}</div>
            </>
        )
    }
}

export default SentenceWriter;