import React from 'react';
import Languages from '../config/languages'
import LanguagesPicker from './LanguagesPicker'
import SentenceWriter from './SentenceWriter'
import Summary from './Summary'


class Root extends React.Component {

    state = {
        language: "",
        summary: null,
        start: false
    }



    componentDidMount() {
        const self = this;

        document.addEventListener('keypress', (e) => {
            if (self.state.language) {
                if (e.key === 'Enter') {
                    self.setState({ start: true })
                }
            }
        });
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.state.language !== prevState.language) {
            this.setState({ start:false});
        }
    }


    onChangeCallback(value) {
        this.setState({ language: value })
    }
    showSummaryCallback(data) {
        this.setState({ summary: data })
    }

    tryAgain(){
        this.setState({language: "",
        summary: null,
        start: false})
    }
    render() {
        return (
            <>
                {
                    !this.state.summary &&
                    <>
                        <LanguagesPicker onChangeCallback={(value) => this.onChangeCallback(value)}></LanguagesPicker>
                        {
                            this.state.language && this.state.start &&
                            <SentenceWriter
                                language={this.state.language}
                                showSummaryCallback={(summary) => this.showSummaryCallback(summary)}></SentenceWriter>


                        }
                    </>

                }

                {
                    this.state.summary &&

                    <>
                    
                    <Summary data={this.state.summary}></Summary>

                    <button onClick = {()=>this.tryAgain()}>try again</button>
                    </>

                }

            </>
        )
    }
}

export default Root;