import React from 'react';
import Languages from '../config/languages'


class LanguagePicker extends React.Component {

    state = {
        value: ""
    }

    onChange(event) {
        this.setState({ value: event.target.value });
        this.props.onChangeCallback(event.target.value);
        this.dropdown.blur();
    }
    render() {
        return (
            <>
                <div style={{padding:"10px"}}>Please select language and press enter to start:</div>

                <select style={{fontSize:"20px"}} ref={(ref) => this.dropdown = ref} onChange={(value) => this.onChange(value)} value={this.state.value}>
                    <option disabled={true} value="">select language</option>
                    {
                        Object.keys(Languages.values).map((item) => <option value={item}>{Languages.values[item]}</option>)
                    }
                </select>
            </>
        )
    }
}

export default LanguagePicker;